import {Component, OnDestroy, OnInit} from '@angular/core';
import {DataService, ProcessedProduct} from "../../../../services/data.service";
import {Subject} from "rxjs";
import {takeUntil} from "rxjs/operators";
import {FormDataService} from "../../../../services/form-data.service";

@Component({
    selector: 'app-product-select',
    templateUrl: './product-select.component.html',
    styleUrls: ['./product-select.component.scss']
})
export class ProductSelectComponent implements OnInit, OnDestroy {

    private readonly destroyed$ = new Subject();

    processedProducts: ReadonlyArray<ProcessedProduct> | null = null;

    constructor(
        private readonly dataService: DataService,
        private readonly formDataService: FormDataService
    ) {
    }

    ngOnInit(): void {
        this.formDataService.processedProducts.pipe(
            takeUntil(this.destroyed$)
        ).subscribe(processedProducts => this.processedProducts = processedProducts);
    }

    ready(): boolean {
        return true;
    }

    select(product: ProcessedProduct | null) {
        this.formDataService.productSelectionControls.selectedProduct.setValue({productId: product?.id});
    }

    ngOnDestroy() {
        this.destroyed$.next();
        this.destroyed$.complete();
    }

}
