import {Component, EventEmitter, Input, OnChanges, OnDestroy, OnInit, Output, SimpleChanges, ViewChild} from '@angular/core';
import {NamedSample} from "../../../../util/data";
import {Observable, Subject, Subscription} from "rxjs";
import {ApiData, DataService} from "../../../../services/data.service";
import {takeUntil} from "rxjs/operators";
import {Components} from "../../../../../generated/api";
import {MoveDirection} from "../../../../util/util-types";
import {MatStepper} from "@angular/material/stepper";
import {FormDataService, SampleFormInfo} from "../../../../services/form-data.service";
import {FormArray} from "@angular/forms";
import {LayoutService} from "../../../../services/layout.service";

interface SampleData {
    sample: NamedSample;
    component: Components | null;
}

@Component({
    selector: 'app-concentration-step',
    templateUrl: './concentration-step.component.html',
    styleUrls: ['./concentration-step.component.scss'],
})
export class ConcentrationStepComponent implements OnInit, OnChanges, OnDestroy {

    private readonly destroyed$ = new Subject();
    private moveSubscription: Subscription | null = null;

    @Input()
    move: Observable<MoveDirection> | null = null;

    @Output()
    outerMove = new EventEmitter<MoveDirection>();

    @ViewChild('stepper')
    stepper: MatStepper;

    sampleData: SampleData[] | null = null;

    data: ApiData | null;

    stepperVertical: boolean = false;

    readonly concentrationForms: FormArray;
    samplesFormInfo: SampleFormInfo[] | null = null;

    constructor(
        private readonly dataService: DataService,
        private readonly layoutService: LayoutService,
        private readonly formDataService: FormDataService
    ) {
        this.concentrationForms = formDataService.concentrationForms;
    }

    ngOnInit(): void {
        this.layoutService.stepperVerticalMode.pipe(
            takeUntil(this.destroyed$)
        ).subscribe(stepperVertical => this.stepperVertical = stepperVertical);

        this.dataService.data.pipe(
            takeUntil(this.destroyed$)
        ).subscribe(data => this.data = data);

        this.formDataService.sampleFormInfo.pipe(
            takeUntil(this.destroyed$)
        ).subscribe(samplesFormInfo => this.samplesFormInfo = samplesFormInfo);
    }

    ready(): boolean {
        return this.samplesFormInfo != null && this.samplesFormInfo.length === this.concentrationForms.length;
    }

    ngOnChanges(changes: SimpleChanges) {
        if (changes.move) {
            if (this.moveSubscription != null) {
                this.moveSubscription.unsubscribe();
                this.moveSubscription = null;
            }
            if (this.move != null) {
                this.move.pipe(
                    takeUntil(this.destroyed$)
                ).subscribe(moveEvent => {
                    if (moveEvent === 'next') {
                        if (this.stepper.selectedIndex === this.stepper.steps.length - 1) {
                            this.outerMove.emit(moveEvent);
                        } else {
                            this.stepper.next();
                        }
                    } else {
                        if (this.stepper.selectedIndex === 0) {
                            this.outerMove.emit(moveEvent);
                        } else {
                            this.stepper.previous();
                        }
                    }
                })
            }
        }
    }

    ngOnDestroy() {
        this.destroyed$.next();
        this.destroyed$.complete();
    }
}
