import {Component, EventEmitter, Input, Output} from '@angular/core';

@Component({
  selector: 'app-prev-next-buttons',
  templateUrl: './prev-next-buttons.component.html',
  styleUrls: ['./prev-next-buttons.component.scss']
})
export class PrevNextButtonsComponent {

  @Input()
  previousEnabled: boolean = true;

  @Input()
  nextEnabled: boolean = true;

  @Input()
  hideNext: boolean = false;

  @Output()
  next = new EventEmitter<void>()

  @Output()
  previous = new EventEmitter<void>()

}
