import { Component, HostListener, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { Observable, Subject } from "rxjs";
import { DataService } from "../../../services/data.service";
import { map, takeUntil } from "rxjs/operators";
import { SampleFormService, Species, UserAssessmentInput } from "../../../../generated/api";
import { StepperSelectionEvent } from "@angular/cdk/stepper/stepper";
import { MatStepper } from "@angular/material/stepper";
import { MoveDirection } from "../../../util/util-types";
import { LoginService } from "../../../services/login.service";
import { FormDataService } from "../../../services/form-data.service";
import { ResultInput, ResultOutput } from "../../../util/data";
import { LayoutService } from "../../../services/layout.service";
import { TranslateService } from "@ngx-translate/core";

@Component({
    selector: 'app-assessment',
    templateUrl: './assessment.component.html',
    styleUrls: ['./assessment.component.scss']
})
export class AssessmentComponent implements OnInit, OnDestroy {

    private readonly destroyed$ = new Subject();

    resultOutput: ResultOutput | null = null;

    species: Species | null = null;
    loggedIn: boolean | null = null;

    private readonly concentrationStepMove$ = new Subject<MoveDirection>();
    readonly concentrationStepMove: Observable<MoveDirection> = this.concentrationStepMove$;

    @ViewChild('stepper')
    stepper: MatStepper;

    stepperVertical: boolean = false;

    currentStep: number = 0;
    onResultStep: boolean = false;
    onProductStep: boolean = false;
    canGoBack: boolean = false;
    canGoNext: boolean = false;

    apiErrors: unknown | null = null;

    constructor(
        private readonly loginService: LoginService,
        private readonly dataService: DataService,
        readonly formDataService: FormDataService,
        private readonly layoutService: LayoutService,
        private readonly translateService: TranslateService,
        private readonly sampleFormService: SampleFormService
    ) {
    }

    @HostListener('window:beforeunload', ['$event'])
    onBeforeUnload(evt: Event) {
        if (this.formDataService.generalForm.dirty) {
            evt.preventDefault();
            evt.returnValue = '' as any;
        }
    }

    ngOnInit(): void {
        this.layoutService.stepperVerticalMode.pipe(
            takeUntil(this.destroyed$)
        ).subscribe(stepperVertical => this.stepperVertical = stepperVertical);

        this.formDataService.species.pipe(
            takeUntil(this.destroyed$)
        ).subscribe(species => this.species = species);

        this.formDataService.currentStep.pipe(
            takeUntil(this.destroyed$)
        ).subscribe(currentStep => {
            this.currentStep = currentStep;
        });

        this.formDataService.onResultStep.pipe(
            takeUntil(this.destroyed$)
        ).subscribe(onResultStep => this.onResultStep = onResultStep);

        this.formDataService.onProductSelectStep.pipe(
            takeUntil(this.destroyed$)
        ).subscribe(onProductStep => this.onProductStep = onProductStep);

        this.formDataService.reset.pipe(
            takeUntil(this.destroyed$)
        ).subscribe(() => this.stepper.reset());

        this.loginService.loggedIn.pipe(
            takeUntil(this.destroyed$)
        ).subscribe(loggedIn => this.loggedIn = loggedIn);

        this.formDataService.resultOutput.pipe(
            takeUntil(this.destroyed$)
        ).subscribe(resultOutput => this.resultOutput = resultOutput);

        this.formDataService.apiErrors.pipe(
            takeUntil(this.destroyed$)
        ).subscribe(apiErrors => {
            this.apiErrors = apiErrors;
        })
    }

    headerHighlight(): Observable<string> {
        return this.translateService.get('HEADER_REGULAR').pipe(
            map((translation: string) => {
                const split = translation.split(' ');
                switch (split.length) {
                    case 0:
                    case 1:
                        return translation;
                    case 2:
                        return split[0];
                    default:
                        return `${split[0]} ${split[1]}`;
                }
            })
        );
    }

    headerRest(): Observable<string> {
        return this.translateService.get('HEADER_REGULAR').pipe(
            map((translation: string) => {
                const split = translation.split(' ');
                switch (split.length) {
                    case 0:
                    case 1:
                        return '';
                    case 2:
                        return ' ' + split[1];
                    default:
                        return ' ' + split.slice(2).join(' ');
                }
            })
        );
    }

    backEnabled(): boolean {
        return this.currentStep > 0 && this.currentStep !== (this.loggedIn ? 4 : 3);
    }

    nextEnabled(): boolean {
        switch (this.currentStep) {
            case 0:
                return this.formDataService.generalForm.valid;
            case 1:
                return this.formDataService.samplesForm.valid && this.formDataService.speciesNewForm.valid;
            // case 2:
            //     return this.formDataService.concentrationForms.valid;
            default:
                return false;
        }
    }

    stepperChange(event: StepperSelectionEvent) {


        this.formDataService.setCurrentStep(event.selectedIndex);
    }

    move(dir: MoveDirection) {
        if (this.stepper.selectedIndex === 2) {
            this.concentrationStepMove$.next(dir);
            this.stepperMove(dir);
        } else if (this.stepper.selectedIndex === 1) {
            let ingredients: string = '';
            let inclusionPercentage: string = '';
            this.formDataService.samplesFormArray.value.forEach(data => {
                if (data.component && data.name) {
                    if (ingredients) {
                        ingredients = `${ingredients},${data.component}`;
                        inclusionPercentage = `${inclusionPercentage},${data.name}`;
                    } else {
                        ingredients = data.component;
                        inclusionPercentage = data.name;
                    }

                }
            })
            this.sampleFormService.sampleFormCreate({
                "species": this.formDataService.speciesNewForm.get("speciesNew").value,
                "ingredients": ingredients,
                "inclusion_percentage": inclusionPercentage
            }).subscribe(resultOutput => {
                this.resultOutput = resultOutput
                this.stepperMove(dir);
                this.onProductStep = true;
            })
        } else {
            this.stepperMove(dir);
        }
    }

    stepperMove(dir: MoveDirection) {
        if (dir === 'next') {
            this.stepper.next();
        } else {
            this.stepper.previous();
        }
    }

    ngOnDestroy() {
        this.destroyed$.next();
        this.destroyed$.complete();
        this.concentrationStepMove$.complete();
    }

}
