import {Component, OnDestroy, OnInit} from '@angular/core';
import {ActivatedRoute} from "@angular/router";
import {combineLatest, Subject} from "rxjs";
import {map, switchMap, takeUntil, tap} from "rxjs/operators";
import {AssessmentService} from "../../../../../generated/api";
import {FormDataService} from "../../../../services/form-data.service";
import {ResultOutput} from "../../../../util/data";

@Component({
    selector: 'app-assessment-detail',
    templateUrl: './assessment-detail.component.html',
    styleUrls: ['./assessment-detail.component.scss']
})
export class AssessmentDetailComponent implements OnInit, OnDestroy {

    private readonly destroyed$ = new Subject();

    loading: boolean = true;
    resultOutput: ResultOutput | null = null;
    externalReport: boolean = false;

    constructor(
        private readonly route: ActivatedRoute,
        private readonly formDataService: FormDataService,
        private readonly assessmentService: AssessmentService,
    ) {
    }

    ngOnInit(): void {
        combineLatest([this.route.paramMap, this.route.queryParamMap]).pipe(
            tap(() => this.loading = true),
            map(([params, queryParams]) => {
                return {
                    id: params.get('id'),
                    secret: params.get('secret'),
                    externalReport: queryParams.get('report_type') === 'external'
                };
            }),
            switchMap(({id, secret, externalReport}) => {
                return this.assessmentService.assessmentRetrieve(id, secret).pipe(
                    map(apiOutput => ({apiOutput, externalReport}))
                )
            }),
            switchMap(({apiOutput, externalReport}) => {
                return this.formDataService.makeResultOutput(apiOutput).pipe(
                    map(resultOutput => ({resultOutput, externalReport}))
                );
            }),
            tap(() => this.loading = false),
            takeUntil(this.destroyed$)
        ).subscribe(({resultOutput, externalReport}) => {
            this.resultOutput = resultOutput;
            this.externalReport = externalReport;
        });


    }

    ngOnDestroy() {
        this.destroyed$.next();
        this.destroyed$.complete();
    }

}
