import {Component, Input, OnDestroy, OnInit} from '@angular/core';
import {ApiData, DataService} from "../../../../services/data.service";
import {Subject} from "rxjs";
import {FormArray, FormGroup} from "@angular/forms";
import {concentrationUnits, FormDataService} from "../../../../services/form-data.service";
import {takeUntil} from "rxjs/operators";


@Component({
    selector: 'app-concentration-form',
    templateUrl: './concentration-form.component.html',
    styleUrls: ['./concentration-form.component.scss'],
})
export class ConcentrationFormComponent implements OnInit, OnDestroy {

    private readonly destroyed$ = new Subject();

    @Input()
    formArray: FormArray;

    data: ApiData | null = null;

    readonly units = concentrationUnits;

    constructor(
        private readonly formDataService: FormDataService,
        private readonly dataService: DataService
    ) {
    }

    ngOnInit(): void {
        this.dataService.data.pipe(
            takeUntil(this.destroyed$)
        ).subscribe(data => this.data = data);
    }

    ready(): boolean {
        return this.data != null && this.formArray != null && this.formArray.length === this.data.mycotoxins.length;
    }

    ngOnDestroy() {
        this.destroyed$.next();
        this.destroyed$.complete();
    }

}
